/* Estilos Globais */
body {
  font-family: 'Arial', sans-serif;
  background-color: #f1f5f8;
  color: #333;
  margin: 0;
  padding: 0;
}

/* Background principal */
.bg-main {
  background-color: #1e293b; /* Azul escuro */
}

/* Container Principal */
.container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 50px 20px;
  background-color: #1e293b; /* Azul escuro */
}

/* Card de Apresentação */
.card {
  background-color: #ffffff;
  border-radius: 8px;
  width: 100%;
  max-width: 850px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  padding: 40px;
  text-align: center;
  font-family: 'Arial', sans-serif;
}

/* Títulos */
.heading {
  font-size: 2.5rem;
  font-weight: 700;
  color: #2563eb; /* Azul mais claro */
  margin-bottom: 20px;
  text-transform: uppercase;
}

/* Subtítulos e Parágrafos */
.paragraph {
  font-size: 1.1rem;
  color: #64748b;
  margin-bottom: 20px;
}

/* Botões */
.button-group {
  display: flex;
  justify-content: center;
  gap: 24px;
  margin-top: 30px;
}

.button {
  padding: 14px 32px;
  border-radius: 50px;
  font-size: 1rem;
  font-weight: 600;
  color: white;
  text-decoration: none;
  transition: all 0.3s ease;
  text-align: center;
  display: inline-block;
}

.bg-blue {
  background-color: #2563eb;  /* Azul brilhante */
}

.bg-blue:hover {
  background-color: #1d4ed8;
}

.bg-orange {
  background-color: #fb923c; /* Laranja moderno */
}

.bg-orange:hover {
  background-color: #f97316;
}

.bg-gray {
  background-color: #4b5563;  /* Cinza neutro */
}

.bg-gray:hover {
  background-color: #374151;
}

/* Links */
.link {
  color: #2563eb;
  text-decoration: none;
  font-weight: 600;
}

.link:hover {
  text-decoration: underline;
}

/* Informações de Contato */
.contact-info {
  margin-top: 30px;
  font-size: 0.9rem;
  color: #4b5563;
}

.contact-info a {
  color: #2563eb;
  text-decoration: none;
  font-weight: 600;
}

.contact-info a:hover {
  text-decoration: underline;
}

/* Animações suaves nos botões */
@keyframes pulse {
  0% {
      transform: scale(1);
  }
  50% {
      transform: scale(1.1);
  }
  100% {
      transform: scale(1);
  }
}

.button:hover {
  animation: pulse 1s ease-in-out infinite;
}
